

.title{
    font-weight: 800;
    margin-bottom: 35px;
    line-height: 25px;
    position: relative;
}
.title h2 span {
    font-weight: 400;
    color: #777;
}
.title::before{
    content: '';
    position: absolute;
    bottom: -20px;
    width: 50px;
    height: 1px;
    background: #777;
    z-index: 1;
    left: 50%;
    transform: translate(-50%, 0%);
}


